<div class="create-feedback"><div class="header"><router-link to="/"><img src="@/assets/navigate-up-arrow.png" alt="">        Go Back</router-link></div><form @submit.prevent="addOrUpdateFeedback(isUpdate)"><div class="icon" v-if="!isUpdate">&plus;</div><div class="icon" v-else=""><img src="@/assets/document-editor.png" alt=""></div><h2 class="form__title" v-if="isUpdate">Update &quot;{{ feedback.TITLE }}&quot;</h2><h2 class="form__title" v-else="">Create New FeedBack</h2><div class="fields"><div class="field"><label for="">Feedback Title</label><small>Add a short, descriptiove headline</small><input required="" type="text" v-model="feedback.TITLE"></div><div class="field"><label for="">Category</label><small>Choose a category for your feedback</small><select required="" name="" id="" v-model="feedback.CATEGORY"><option value="" disabled="" selected="">Select an category</option><option value="UX">UX</option><option value="UI">UI</option><option value="LIVE">Live</option><option value="ENCHANCEMENT">Enchancemet</option><option value="BUG">Bug</option><option value="FEATURE">Feature</option></select></div><div class="field"><label for="">Feedback Detail</label><small>Include any specific, comments on what should be improved, added,
etc.</small><textarea name="" id="" cols="30" rows="10" required="" v-model="feedback.DETAIL"></textarea></div></div><div class="buttons"><router-link class="cancel" to="/">Go back</router-link><button class="submit"><span v-if="isUpdate"> Update </span><span v-else=""> Submit </span>          Feedback</button></div></form></div>